import {useEffect, useRef, useState} from "react";
import Peer from "peerjs";

import styles from './styles.module.scss'

const App = () => {
  const [localId, setLocalId] = useState('');
  const [remoteId, setRemoteId] = useState('');

  const currentCall = useRef();
  const currentConnect = useRef();

  const peer = useRef()

  const localVideo = useRef();
  const remoteVideo = useRef();

  useEffect(() => {
    createPeer()

    return () => {
      endCall()
    }
  }, [])

  const endCall = () => {
    if (currentCall.current) {
      currentCall.current.close()
    }
  }

  const createPeer = () => {
    peer.current = new Peer();
    peer.current.on("open", (id) => {
      setLocalId(id)
    });

    // 
    peer.current.on('connection', (connection) => {
      connection.on('data', (data) => {
        console.log('', data);
      })
      connection.send('')

      currentConnect.current = connection
    })

    // 
    peer.current.on('call', async (call) => {
      if (window.confirm(` ${call.peer}?`)) {
        // 
        const stream = await navigator.mediaDevices.getUserMedia({ video: true, audio: true })
        localVideo.current.srcObject = stream
        localVideo.current.play()

        // 
        call.answer(stream)

        //  remoteVideo 
        call.on('stream', (stream) => {
          remoteVideo.current.srcObject = stream;
          remoteVideo.current.play()
        })

        currentCall.current = call
      } else {
        call.close()
        alert('')
      }
    })
  }

  const callUser = async () => {
    // 
    const stream = await navigator.mediaDevices.getUserMedia({ video: true, audio: true })
    localVideo.current.srcObject = stream
    localVideo.current.play()

    // 
    const connection = peer.current.connect(remoteId);
    connection.on('open', () => {
      connection.send('Hi')
    })
    currentConnect.current = connection

    // 
    const call = peer.current.call(remoteId, stream)
    call.on("stream", (stream) => {
      remoteVideo.current.srcObject = stream;
      remoteVideo.current.play()
    });
    call.on("error", (err) => {
      console.error(err);
    });
    call.on('close', () => {
      endCall()
    })

    currentCall.current = call
  }

  return (
    <div>
      <p> Peer ID: {localId}</p>
      <input value={remoteId} onChange={e => setRemoteId(e.target.value)} type="text" placeholder=" Peer  Id"/>
      <button onClick={callUser}></button>
      <button id="end-call" onClick={endCall}></button>

      <div className={styles.live}>
        <video controls className={styles.localVideo} ref={localVideo} />
        <video controls className={styles.remoteVideo} ref={remoteVideo} muted />
      </div>
    </div>
  );
}

export default App;
